	module.exports = {"data":{"markdownRemark":{"html":"<p><strong>⛅</strong></p>\n<p>今日は朝から体調が悪く、半ドンかつ一斉下校の日で不本意でしたが、両親に迎えに来てもらいました。</p>\n<p>帰宅後昼食をとったのですが、気分が悪く殆ど手をつけられませんでした。\n午後、父と弟が買い出しに行き、母が間食のお好み焼きを作ってくれました。\nこれは少しだけ口にできました。</p>\n<p>それから暫く安静の後、通院しましたが、大したことはないと整腸剤だけ戴き、夕方頃にはほぼ治ったので安心しました。</p>\n<p>夕食が美味しかったです。</p>","frontmatter":{"cover":null,"date":"1990-11-10T21:00:00+09:00","strDate":"1990/11/10","title":"日記"}}},"pathContext":{"path":"/ja/blog/1990/11/10-diary/","langKey":"ja"}}

